.container {
    position: absolute;
    /* this is the default height and width */
    /* overriden by js */
    height: 100%;
    width: 100%;
}

.center {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
        -ms-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
}

.errorText {
    text-align: center;
    font-size: 16px;
}



